import React from "react";

function AboutApp() {
  return (
    <div className="about" id="about-app">
      <h1 className="about__title">O QUE ?</h1>
      <h4 className="about__text">
        O AM I SAFE?  um aplicativo que visa ajudar todas as pessoas que no
        conhecem a cidade de So Paulo a se sentirem mais seguras quando
        estiverem l, seja visitando ou a trabalho. Baseado em um banco de dados
        de 7 meses para trs, o aplicativo  capaz de dizer qual o nvel de
        segurana de um bairro especfico. O funcionamento  simples: basta
        escolher o bairro que voc deseja pesquisar e o aplicativo imediatamente
        te retornar com uma pontuao de 'segurana' e ainda te sugerir outras
        opes mais seguras, caso sua opo no seja.
      </h4>
    </div>
  );
}

export default AboutApp;
